import * as Yup from 'yup';
import { useState } from 'react';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Link from '@mui/material/Link';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputAdornment from '@mui/material/InputAdornment';
// hooks
import { useBoolean } from 'src/hooks/use-boolean';
// routes
import { paths } from 'src/routes/paths';
import { RouterLink } from 'src/routes/components';
import { useSearchParams, useRouter } from 'src/routes/hook';
// config
import { PATH_AFTER_LOGIN } from 'src/config-global';
// auth
import { useAuthContext } from 'src/auth/hooks';
// components
import Iconify from 'src/components/iconify';
import { Box, TextField } from '@mui/material';
import axios, { endpoints } from 'src/utils/axios';

// ----------------------------------------------------------------------

export default function JwtRegisterView() {
  const { register } = useAuthContext();

  const router = useRouter();

  const [errorMsg, setErrorMsg] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const searchParams = useSearchParams();


  const nameValue = searchParams.get('name');
  const emailValue = searchParams.get('email');
  const uuidValue = searchParams.get('uuid');

  // Split the full name into an array of first and last name
  const nameParts = nameValue.split(" ");

  const password = useBoolean();

  const RegisterSchema = Yup.object().shape({
    firstName: Yup.string().required('First name required'),
    lastName: Yup.string().required('Last name required'),
    email: Yup.string().required('Email is required').email('Email must be a valid email address'),
    password: Yup.string()
      .required('Password is required')
      .matches(
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
        'Password must contain at least 8 characters, 1 number, 1 symbol, 1 uppercase letter, and 1 lowercase letter'
      ),
  });


  const defaultValues = {
    firstName: nameParts[0],
    lastName: nameParts[nameParts.length - 1],
    email: emailValue,
    password: '',
    confirmPassword: ''
  };

  const [user, setUser] = useState(defaultValues)

  const handleChange = (event) => {
    const { name, value } = event.target;
    setUser((prevUser) => ({
      ...prevUser,
      [name]: value,
    }));
  };

  const handleRegister = async () => {
    setIsSubmitting(true);

    try {
      //  Validate the schema 
      await RegisterSchema.validate(user, { abortEarly: false });
      setErrorMsg('');

      const response = await axios.post(endpoints.auth.passwordTable.details, { email: user.email });

      if (response.data.data) {
        const { uuidCode, expiryDate } = response.data.data;
        const expiryDateUTC = new Date(expiryDate); // Convert UTC string to Date object
        const currentDate = new Date(); // Current date

        if (uuidCode === uuidValue && expiryDateUTC > currentDate) {
          // console.log('All Right you can login now');
          // Add your registration logic here
          await register?.(user.email, user.password, user.firstName, user.lastName);
          router.push(PATH_AFTER_LOGIN);
        } else {
          setErrorMsg('The link is expired');
        }
      } else {
        setErrorMsg('The link is expired');
      }
    } catch (error) {
      // console.error(error);
      if (error.errors && error.errors.length > 0) {
        setErrorMsg(error.errors[0]);
      } else {
        setErrorMsg(typeof error === 'string' ? error : error.error);
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  const renderForm = (
    <div>
      {!!errorMsg && <Alert severity="error">{errorMsg}</Alert>}

      {/* <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}> */}
      <TextField
        name="firstName"
        label="First name"
        fullWidth
        value={user.firstName}
        onChange={handleChange}
        sx={{ mt: 4, backgroundColor: 'white' }}
        required
      />
      <TextField
        name="lastName"
        label="Last name"
        fullWidth
        value={user.lastName}
        onChange={handleChange}
        sx={{ mt: 4, backgroundColor: 'white' }}
        required
      />
      <TextField
        name="email"
        label="Email address"
        fullWidth
        disabled
        value={user.email}
        onChange={handleChange}
        sx={{ mt: 4, backgroundColor: 'white' }}
        required
      />
      <TextField
        name="password"
        label="Password"
        fullWidth
        type='password'
        value={user.password}
        onChange={handleChange}
        sx={{ mt: 4, backgroundColor: 'white' }}
        required
      />
      <Typography sx={{ mb: 2.5 }} variant="caption" display="block" gutterBottom>At least 8 characters, 1 number, 1 symbol, 1 upper case and lower case</Typography>
      <TextField
        name="confirmPassword"
        label="Confirm Password"
        fullWidth
        value={user.confirmPassword}
        onChange={handleChange}
        required
        type={password.value ? 'text' : 'password'}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton onClick={password.onToggle} edge="end">
                <Iconify icon={password.value ? 'solar:eye-bold' : 'solar:eye-closed-bold'} />
              </IconButton>
            </InputAdornment>
          ),
        }}
        sx={{ backgroundColor: 'white' }}
      />
      <LoadingButton
        fullWidth
        color="primary"
        sx={{ mt: 4, borderRadius: "18px" }}
        size="large"
        variant="contained"
        disabled={
          user.firstName === '' ||
          user.lastName === '' ||
          user.email === '' ||
          user.password === '' ||
          user.confirmPassword === ''
        }
        loading={isSubmitting}
        onClick={handleRegister}
      >
        SIGN UP NOW
      </LoadingButton>
      <Stack direction="row" spacing={0.5} justifyContent='center'>
        <Typography variant="body2"> Already have an account? </Typography>

        <Link href={paths.auth.jwt.login} component={RouterLink} variant="subtitle2" color='#074DFF'>
          SIGN IN
        </Link>
      </Stack>
    </div >
  );

  return (
    <div style={{ backgroundColor: "#F3F8FB", display: 'flex', alignItems: 'center', flexDirection: 'column', width: '100vw', height: '800px', gap: '30px' }}>
      {/* {renderHead} */}

      <div style={{ width: '600px', }}>

        <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '30px' }}>

          <Box
            component="img"
            src="/logo/logo_single.svg"
            sx={{ width: 120, height: 120, display: 'flex', justifyContent: 'center' }}
          />
        </div>

        {renderForm}
      </div>

      {/* {renderTerms} */}

    </div>
  );
}
